@import '../variables.scss';
@import "./auth-form.scss";
@import "../mixins.scss";



* {
    padding : 0;
    margin : 0;
    box-sizing: border-box;
    font-family: 'PT Sans',sans-serif;
}

#content {
    min-height : 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color : #303030;

    .wave {
        position: absolute;
        top : 0;
        left : 0;
        width : 100%;
        z-index: 1;
        transform-origin : top;
        display: block;
        animation : animateWave 1s ease;
    }

    .title-form {
        position: relative;
        z-index : 2;
        color : white;
        margin-top : 5.7rem;
    }

    form {
        flex : 0 0 100%;
        z-index: 2;
        input {
            color : white;
        }

        .input-wrapper {
            margin-top : 1.5rem;
            display: flex;
            justify-content: center;
            flex-wrap : wrap;
            align-items: flex-start;

            .input-group,
            .input-group-select {
                flex : 1 0 30%;
                margin : 2rem 1rem;
                height: auto;
                display : flex;
                flex-direction : column;
            }



            .input-group-select ul {
                width: 100%;
                display: flex;
                flex-wrap : wrap;
                outline : none;
                border : none;
                list-style-type: none;
            }

            .input-group-select select {
                margin-top : 1rem;
                width : 100%;
                border : none;
                outline : none;
                background-color: transparent;
                color : white;

                option {
                   color :  #303030;
                }
            }

                        

                .tag-item {
                    margin : .25rem;
                    background-color : $base-color-pink;
                    padding : .3rem .5rem;
                    border-radius : 10px;
                    display: flex;
                    align-items: center;
                    p {
                        color: white;
                        @include font-scale(1rem);
                        font-weight : 500;
                    } 

                    i {
                        color : white;
                        margin-left : .5rem;
                        cursor : pointer;
                    }
                }
            }


        }   
    

        input[type="text"]:focus,
        input[type="text"]:valid,
        input[type="password"]:focus,
        input[type="password"]:valid,
        input[type="number"]:focus,
        input[type="number"]:valid {
            border-bottom : 2px solid white;
        }
    
    
        input[type="text"]:valid ~ label,
        input[type="text"]:focus ~ label,
        input[type="password"]:valid ~ label,
        input[type="password"]:focus ~ label,
        input[type="number"]:valid ~ label,
        input[type="number"]:focus ~ label {
            color : white;
        }
    
        input[type="text"]:valid ~ i,
        input[type="text"]:focus ~ i,
        input[type="password"]:valid ~ i,
        input[type="password"]:focus ~ i,
        input[type="number"]:valid ~ i,
        input[type="number"]:focus ~ i {
            color : white;
        }
    
        .forgot-password {
            margin-top : .5rem;
            text-align: center;
            transform: scale(1);
            transition : .3s
        }

        .forgot-password:hover {
            color : white;
            transform : scale(1.05);
        }
    
        .btn-submit {
            background-image: linear-gradient(to left,$base-color-pink,#f74d86);
        }
}


    @keyframes animateWave {
        0%  {
            transform : scaleY(0.8);
        }
        100% {
            transform  : scaleY(1);
        }
}